body.projects-home {
  background-color: hsl(0, 0%, 2%);
  min-height: 150vh;
  padding-top: 200px;
  font-family: "Roboto", sans-serif;
}

body.project-history,
body.projects-home {
  scrollbar-width: none;
}
body.project-history::-webkit-scrollbar,
body.projects-home::-webkit-scrollbar {
  width: 0;
  height: 0;
  background-color: hsla(0, 0%, 0%, 0);
}
body.project-history::-webkit-scrollbar-thumb,
body.projects-home::-webkit-scrollbar-thumb {
  background-color: hsla(0, 0%, 0%, 0);
}

body.project-history .see-auto-screenshots .screenshots::-webkit-scrollbar {
  width: 2em;
  height: 1em;
}
body.project-history .see-auto-screenshots .screenshots::-webkit-scrollbar-track {
  background: hsla(261, 100%, 20%, 0.39);
  border-radius: 100vw;
  margin-block: 0.5em;
}
body.project-history .see-auto-screenshots .screenshots::-webkit-scrollbar-thumb {
  background: hsla(261, 100%, 20%, 0.69);
  border: 0.05em solid hsla(261, 100%, 50%, 0.69);
  border-radius: 100vw;
}
body.project-history .see-auto-screenshots .screenshots::-webkit-scrollbar-thumb:hover {
  background: hsla(261, 100%, 50%, 0.69);
}

body.project-history nav,
body.project-code-analytics nav {
  height: 40px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  -webkit-backdrop-filter: blur(0.3rem);
  backdrop-filter: blur(0.3rem);
  border: solid 1px transparent;
  border-bottom-color: rgba(255, 255, 255, 0.116);
}
body.project-history nav a,
body.project-code-analytics nav a {
  text-decoration: none;
  color: #00b1dd;
  font-size: 1.1rem;
  font-weight: 500;
  font-family: "Lobster", cursive;
  text-align: center;
  padding-inline: 20px;
}
body.project-history nav a:hover,
body.project-code-analytics nav a:hover {
  text-shadow: 0 0 2px;
}

body.project-history header {
  padding-top: 40px;
}
body.project-history header h1 {
  color: #fff;
  text-align: center;
  padding-block: 5px;
  font-family: "Lobster", cursive;
}
body.project-history header .wrapper {
  margin-inline: auto;
  width: 100%;
  max-width: 1200px;
}
body.project-history header iframe {
  aspect-ratio: 16/9;
  width: 100%;
  max-width: 1200px;
  margin-bottom: 5px;
}

body.project-history {
  background-color: hsl(0, 0%, 2%);
  padding-bottom: 100px;
}

body.project-history main {
  padding-block: 50px;
}
body.project-history main section {
  overflow: hidden;
  height: 400px;
  background-image: linear-gradient(90deg, transparent, hsla(283, 100%, 18%, 0.301));
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-block: 100px;
}

body.project-history main section .yt-autoscreenshot {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-block: 15px;
}
body.project-history main section .yt-autoscreenshot .wrapper {
  height: 100%;
  width: 100%;
  max-width: 600px;
}
body.project-history main section .yt-autoscreenshot .wrapper iframe {
  aspect-ratio: 16/9;
  width: 100%;
}
body.project-history main section .yt-autoscreenshot .wrapper button {
  padding: 5px 15px;
  border: none;
  background-color: hsla(253, 100%, 50%, 0.322);
  text-align: center;
  color: #fff;
  display: block;
  margin-inline: auto;
  cursor: pointer;
}

body.project-history main section .commit-description {
  font-family: "Roboto", sans-serif;
  padding: 10px 20px;
}
body.project-history main section .commit-description .commit-time {
  color: #fff;
  text-align: center;
  padding-block: 5px;
  font-size: 1rem;
}
body.project-history main section .commit-description .commit-title {
  color: hsla(0, 0%, 100%, 0.5);
  text-align: left;
  padding-block: 5px;
  font-size: 1rem;
}
body.project-history main section .commit-description .commit-title svg {
  width: 10px;
  filter: invert(50%);
  margin-inline: 5px;
}
body.project-history main section .commit-description .commit-title span {
  font-weight: 600;
  margin-inline: 15px 5px;
}
body.project-history main section .commit-description .commit-title span:nth-of-type(1) {
  color: hsl(249, 100%, 63%);
  margin-inline: 0 5px;
}
body.project-history main section .commit-description .commit-title span:nth-of-type(2) {
  color: hsl(120, 100%, 40%);
}
body.project-history main section .commit-description .commit-title span:nth-of-type(3) {
  color: hsl(0, 100%, 40%);
}
body.project-history main section .commit-description__summary {
  color: red;
}
body.project-history main section .commit-description__summary ul {
  list-style: none;
  color: hsla(0, 0%, 100%, 0.85);
}
body.project-history main section .commit-description__summary ul li {
  margin-block: 10px;
  position: relative;
}
body.project-history main section .commit-description__summary ul li::before {
  content: "";
  position: absolute;
  transform: translate(-100%, 50%);
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-left-color: hsla(305, 100%, 43%, 0.849);
}

body.project-history main section .commit-description .github-commit {
  height: 50px;
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  margin-block: 20px;
  background-color: hsla(0, 0%, 100%, 0.04);
  border-radius: 10px;
}
body.project-history main section .commit-description .github-commit h2 {
  font-size: 1.2rem;
  color: hsla(0, 0%, 100%, 0.8);
  line-height: 1.7;
}
body.project-history main section .commit-description .github-commit__view-btn {
  height: 35px;
}
body.project-history main section .commit-description .github-commit__view-btn .tooltip:hover::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translate(-50%, 25px);
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-bottom-color: hsla(0, 0%, 100%, 0.3);
}
body.project-history main section .commit-description .github-commit__view-btn .tooltip:hover::after {
  position: absolute;
  white-space: nowrap;
  color: #fff;
  background-color: hsla(0, 0%, 100%, 0.3);
  padding: 3px 10px;
  font-size: 0.8rem;
  border-radius: 2px;
  transform: translate(-100%, 35px);
}
body.project-history main section .commit-description .github-commit__view-btn .tooltip {
  transition: background-color 200ms;
}
body.project-history main section .commit-description .github-commit__view-btn .tooltip:hover {
  background-color: hsla(0, 0%, 72%, 0.26);
}
body.project-history main section .commit-description .github-commit__view-btn button,
body.project-history main section .commit-description .github-commit__view-btn a:nth-of-type(1),
body.project-history main section .commit-description .github-commit__view-btn a:nth-of-type(2) {
  cursor: pointer;
  background-color: transparent;
  position: relative;
  border: 1px solid hsla(0, 0%, 72%, 0.26);
  padding: 5px 15px;
  height: 32px;
}
body.project-history main section .commit-description .github-commit__view-btn svg {
  filter: invert(50%);
  height: 16px;
  width: 16px;
}
body.project-history main section .commit-description .github-commit__view-btn svg.svg-copied {
  display: none;
  filter: invert(50%) sepia(93%) saturate(1352%) hue-rotate(87deg) brightness(119%) contrast(119%);
}
body.project-history main section .commit-description .github-commit__view-btn button {
  border-radius: 5px 0 0 5px;
}
body.project-history main section .commit-description .github-commit__view-btn button:hover.tooltip::after {
  content: "Copy the full SHA";
}
body.project-history main section .commit-description .github-commit__view-btn a:nth-of-type(1) {
  color: rgb(255, 0, 234);
}
body.project-history main section .commit-description .github-commit__view-btn a:nth-of-type(1):hover.tooltip::after {
  content: "View commit details";
}
body.project-history main section .commit-description .github-commit__view-btn a:nth-of-type(2) {
  border-radius: 0 5px 5px 0;
}
body.project-history main section .commit-description .github-commit__view-btn a:nth-of-type(2):hover.tooltip::after {
  content: "Browse the repository at this point in the history";
}

body.project-history .see-auto-screenshots {
  position: fixed;
  inset: 0;
  background-color: hsla(0, 0%, 0%, 0.7);
  z-index: 10;
}

body.project-history .see-auto-screenshots .wrapper-screenshots {
  transform: translateY(20%);
  display: flex;
  flex-direction: column;
}
body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view {
  position: relative;
}

body.project-history .see-auto-screenshots .screenshots {
  overflow: auto;
  display: flex;
  flex-wrap: nowrap;
  gap: 10px;
  padding-block: 20px;
  background-color: hsla(0, 0%, 0%, 0.871);
}
body.project-history .see-auto-screenshots .screenshots img {
  width: 200px;
  opacity: 0.8;
  filter: grayscale(50%);
  cursor: pointer;
}
body.project-history .see-auto-screenshots .screenshots img.active {
  box-shadow: 0 0 7px 3px #000000;
  filter: saturate(120%) grayscale(0%) brightness(120%);
  opacity: 1;
}

body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view .prev,
body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  padding: 16px;
  margin-top: -50px;
  color: hsl(0, 0%, 100%);
  font-weight: bold;
  font-size: 20px;
  border-radius: 0 3px 3px 0;
  user-select: none;
  -webkit-user-select: none;
  background-color: hsla(0, 0%, 0%, 0.15);
  transition: background-color 100ms ease-in-out;
}
body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view .next {
  right: 0;
  border-radius: 3px 0 0 3px;
}
body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view .prev {
  left: 0;
  border-radius: 0 3px 3px 0;
}
body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view .prev:hover,
body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view .next:hover,
body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view .close-btn:hover {
  background-color: hsla(0, 0%, 0%, 0.8);
}
body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view .prev:active,
body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view .next:active {
  color: rgb(234, 0, 255);
}
body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view .close-btn {
  color: #fff;
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: hsla(0, 0%, 0%, 0.15);
  font-size: 2rem;
  border: none;
  border-radius: 50%;
  font-family: "Roboto", sans-serif;
  cursor: pointer;
  width: 40px;
  height: 40px;
  line-height: 0.6;
  transition: background-color 100ms ease-in-out;
}

body.project-history .see-auto-screenshots .wrapper-screenshots .screenshot-in-view .img-number {
  color: #fff;
  padding: 10px;
  position: absolute;
  top: 0;
  left: 0;
  background-color: hsla(0, 0%, 0%, 0.685);
  font-family: "Roboto", sans-serif;
  border-radius: 0 0 5px 0;
}

.project__inner-container__description {
  font-size: 1.2rem;
}
.project__inner-container__description h3 {
  padding-block: 10px;
  color: #fff;
}
.project__inner-container__description article {
  color: hsla(0, 0%, 100%, 0.65);
  width: 600px;
  font-size: 1rem;
}

.project__inner-container__github {
  margin-top: 20px;
  font-size: 1.2rem;
  display: flex;
  justify-content: space-around;
}
.project__inner-container__github a {
  color: rgba(255, 255, 255, 0.9254901961);
  padding: 5px 20px;
  background: rgba(170, 0, 162, 0.0941176471);
  border: none;
  position: relative;
  cursor: pointer;
  transition: background-color 200ms ease-in-out;
}
.project__inner-container__github a:nth-of-type(1):hover {
  background-color: hsla(303, 100%, 50%, 0.3);
}
.project__inner-container__github a:nth-of-type(2):hover {
  background-color: hsla(189, 100%, 50%, 0.3);
}
.project__inner-container__github a:nth-of-type(3):hover {
  background-color: hsla(125, 100%, 50%, 0.3);
}
.project__inner-container__github a:nth-of-type(4):hover {
  background-color: hsla(0, 100%, 50%, 0.3);
}

.projects-home .project__inner-container__project-info {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding-block: 5px;
}
.projects-home .project__inner-container__project-info .date p {
  color: hsla(0, 0%, 100%, 0.5);
  font-size: 0.9rem;
  margin-bottom: 2px;
}
.projects-home .project__inner-container__project-info .date p span {
  padding-left: 2px;
}
.projects-home .project__inner-container__project-info .date p:nth-of-type(1) span {
  color: hsla(145, 100%, 55%, 0.7);
}
.projects-home .project__inner-container__project-info .date p:nth-of-type(2) span {
  color: hsla(342, 100%, 55%, 0.7);
}
.projects-home .project__inner-container__project-info .date p:nth-of-type(3) span {
  color: hsla(313, 100%, 55%, 0.7);
}
.projects-home .project__inner-container__project-info .date p:nth-of-type(4) span {
  color: hsl(0, 0%, 100%);
  filter: drop-shadow(0 0 7px rgba(255, 0, 0, 0.521));
}
.projects-home .project__inner-container__project-info .date p:nth-of-type(5) span {
  color: hsla(249, 100%, 58%, 0.938);
}
.projects-home .project__inner-container__project-info .languages img {
  margin-inline: 4px;
  width: 50px;
}

.project {
  padding-block: 20px;
  background-color: hsla(283, 100%, 18%, 0.144);
  margin-block: 100px;
}
.project h2 {
  color: #fff;
  text-align: center;
  font-weight: 400;
  padding-bottom: 10px;
  font-family: "Lobster", cursive;
}
.project__github-repo-info {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.project__github-repo-info img {
  margin: 4px;
}
.project__inner-container {
  width: 800px;
  margin-inline: auto;
}

.project__inner-container__yt-review {
  padding-block: 20px;
  display: flex;
  justify-content: center;
}
.project__inner-container__yt-review iframe {
  width: 800px;
  height: 420px;
}

body.project-code-analytics {
  background-color: hsl(0, 0%, 2%);
  padding-bottom: 100px;
  padding-top: 50px;
}
body.project-code-analytics h1 {
  color: #fff;
  text-align: center;
  padding-block: 5px;
  font-family: "Lobster", cursive;
}